<template>
    <div v-show="navbarIsShow" class="fixed-top section navbar" style="width:100%;height:100%;padding:0;opacity:0">
        <button class="btn" @click="closeNavbar" style="font-family: cream;position:absolute;left:1%;top:1%;z-index:5001">X</button>
        <div class="col" style="width:100%;height:100%;padding-left:4%;padding-top:10%">
            <div class="row no-gutters"></div>
            <div class="row no-gutters">
                <p>ALL</p>
            </div>
            <div class="row no-gutters">
                <p>NECKLACE</p>
            </div>
            <div class="row no-gutters">
                <p>RING</p>
            </div>
        </div>
    </div>
</template>

<script>
import { gsap } from "gsap";

export default {
    props: {
        navbarIsShow: {
            type: Boolean,
        },
        logoIsShow: {
            type: Boolean,
        },
    },
    components: {},
    data() {
        return {};
    },
    methods: {
        closeNavbar() {
            this.$emit("closenav");
        },
    },
    watch: {
        navbarIsShow(isShow) {
            if (isShow) {
                gsap.to(".navbar", { duration: 0.2, startAt: { opacity: 0 }, opacity: 0.9 });
            }
        },
    },
};
</script>

<style scoped>
.section {
    background: white;
}
p {
    font-family: cream;
    line-height: 1.5vw;
    font-size: 2vw;
}
</style>
