<template>
    <div class="row no-gutters section">
        <div class="col">
            <template v-if="ratio >= 1.2">
                <img class="img-fluid img-rwd img-rwd-color " src="../../../assets/images/page-home/section-top/20210115-DSCF9313-min.png" />
                <img class="img-fluid img-rwd " src="../../../assets/images/page-home/section-top/20210115-DSCF9313-min-gray-min.png" />
                <!-- <img class="img-fluid  img-rwd" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-min-gray-min.png" /> -->

                <img class="" style="width:30vw;z-index:1001;position:absolute;left:10%;margin-top:40vh" src="../../../assets/images/page-home/logo.png" />
            </template>
            <template v-if="ratio >= 0.67 && ratio < 1.2">
                <img class="img-fluid  img-rwd img-rwd-color" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-2-min.png" />
                <img class="img-fluid img-rwd " src="../../../assets/images/page-home/section-top/20210115-DSCF9313-2-min-2.png" />

                <img class="" style="width:25vw;z-index:2001;position:absolute;left:5%;margin-top:60vh" src="../../../assets/images/page-home/logo.png" />
            </template>
            <template v-if="ratio < 0.67">
                <img class="img-fluid img-rwd img-rwd-color" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-3-min.png" />
                <img class="im-fluid img-rwd" src="../../../assets/images/page-home/section-top/20210115-DSCF9313-3-min-2.png" />
                <img class="" style="width:55vw;margin-top:60vh;left:5vw;z-index:2001;position:absolute" src="../../../assets/images/page-home/logo.png" />
            </template>
        </div>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export default {
    data() {
        return {
            screenWidth: window.innerWidth,
            screenHeight: window.innerHeight,
            ratio: window.innerWidth / window.innerHeight,
            // ratio: screen.width / screen.height,
        };
    },
    mounted() {
        let pos = "0 -15vh";
        let size = "120vw 120vh";

        if (this.ratio >= 0.67 && this.ratio < 1.2) {
            pos = "0 0px";
        } else if (this.ratio < 0.67) {
            size = "150vw 150vh";
            pos = "30px -100px";
        }

        gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);
        var tl = gsap.timeline(); //建立動畫時間線
        tl.set(".img-rwd-color", { maskSize: size, maskPosition: pos });
        tl.to(".img-rwd-color", { duration: 0.5, delay: 1, clipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)" });
        tl.to(".img-rwd-color", { duration: 8, ease: "none", maskSize: "10000vw 10000vh", maskPosition: "-5000vw -5000vh" });
    },
};
</script>

<style scoped>
.section {
    height: 100vh;
}

.img-rwd {
    position: absolute;
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.img-mb-rwd {
    position: absolute;
    object-position: 40% -400px;
    object-fit: cover;
    height: 200%;
    width: 100%;
}

.img-rwd-color {
    z-index: 1000;
    mask: url("../../../assets/images/page-home/section-top/mask-image.svg");
    -webkit-mask: url("../../../assets/images/page-home/section-top/mask-image.svg");

    clip-path: polygon(0% 0%, -20% 0%, -0% 100%, 0% 100%);
    /* clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%); */
    mask-repeat: no-repeat;
    -webkit-mask-repeat: no-repeat;
    mask-size: clamp(1200px, 100%, 100vw) 100%;
    -webkit-mask-size: clamp(1200px, 100%, 100vw) 100%;

    -webkit-mask-position: 300px 0px;
    /* mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh);
    -webkit-mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh); */
}

.img-rwd-mb-color {
    z-index: 1000;
    mask: url("../../../assets/images/page-home/section-top/mask-image.svg");
    -webkit-mask: url("../../../assets/images/page-home/section-top/mask-image.svg");

    clip-path: polygon(0% 0%, -20% 0%, -0% 100%, 0% 100%);
    /* clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%); */
    mask-repeat: no-repeat;
    -webkit-mask-repeat: no-repeat;
    mask-size: clamp(1200px, 100%, 100vw) 100%;
    -webkit-mask-size: clamp(1200px, 100%, 100vw) 100%;
    /* mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh);
    -webkit-mask-position: calc((100vw - 1920px) * 0.11) clamp(-3vh, -4vh, -5vh); */
}
</style>
