<template>
    <!-- <label>{{ this.$store.state.count }}</label> -->
    <div class="container-fluid" style="padding:0;">
        <navbar class="navbar" :navbarIsShow="navbarIsShow" @closenav="navbarIsShow = false" />
        <div>
            <img class="logo" style="width:15vw;z-index:1001;position:absolute;opacity:0;margin:2% 0 0 3%" src="../../assets/images/page-home/logo.png" />
            <img class="list" @click="listClick" style="width:4vw;z-index:1001;position:absolute;opacity:0;margin:0.75% 0 0 0.15%" src="../../assets/images/page-home/list.png" />
        </div>

        <swiper class="swiper" ref="mySwiper" :options="swiperOption" style="height:100vh">
            <swiper-slide>
                <sections1 class="section1" />
            </swiper-slide>
            <swiper-slide>
                <sections2_1 class="section2_1" :swiperState="swiperState" />
            </swiper-slide>
            <swiper-slide>
                <sections2 class="section2" :swiperState="swiperState" />
            </swiper-slide>
            <swiper-slide>
                <sections3 class="section3" :swiperState="swiperState" />
            </swiper-slide>
            <swiper-slide>
                <sections4 class="section4" />
            </swiper-slide>
            <swiper-slide>
                <sections5 class="section5" :swiperState="swiperState" />
            </swiper-slide>
        </swiper>
    </div>
</template>
<script>
import sections1 from "./sections/sections1.vue";
import sections2 from "./sections/sections2.vue";
import sections2_1 from "./sections/sections2_1.vue";

import sections3 from "./sections/sections3.vue";
import sections4 from "./sections/sections4.vue";
import sections5 from "./sections/sections5.vue";
import navbar from "./sections/navbar.vue";

import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";

export default {
    components: {
        sections1,
        sections2,
        sections2_1,
        sections3,
        sections4,
        sections5,
        navbar,
        Swiper,
        SwiperSlide,
    },

    data() {
        let self = this;
        return {
            navbarIsShow: false,
            swiperState: { state: "", index: 0 },
            bindContent: "",
            swiperOption: {
                direction: "vertical",
                slidesPerView: 1,
                speed: 500,
                spaceBetween: 0,
                mousewheel: true,
                on: {
                    activeIndexChange: function() {
                        if (this.activeIndex == "0") {
                            gsap.to(".logo", { opacity: 0 });
                            gsap.to(".list", { opacity: 0 });
                        }
                        if (this.activeIndex == "1") {
                            gsap.to(".list", { opacity: 0.6 });

                            gsap.to(".logo", { opacity: 1 });
                        }
                        self.swiperState = { state: "", index: this.activeIndex };
                    },
                },
            },
        };
    },

    mounted() {
        gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);
    },
    methods: {
        listClick() {
            console.log("listClick");
            this.navbarIsShow = true;
        },
    },
};
</script>

<style></style>
