<template>
    <!-- <label>{{ this.$store.state.count }}</label> -->
    <div class="container-fluid" style="padding:0;">
        <navbar class="navbar" :navbarIsShow="navbarIsShow" @closenav="(navbarIsShow = false), (logoIsShow = true)" />
        <div class="fixed-top" v-show="logoIsShow" :logoIsShow="logoIsShow">
            <img class="logo" style="width:12.5vw;z-index:1001;position:absolute;margin:2% 0 0 3%;opacity:1" src="../../assets/images/page-home/logo.png" />
            <img class="logo list" @click="listClick" style="width:4vw;z-index:1001;position:absolute;margin:0.75% 0 0 0.15%;opacity:0.6" src="../../assets/images/page-home/list.png" />
        </div>
        <section1 class="section1" />
        <section2 class="section2" />
        <section3 class="section3" />
        <section4 class="section4" />
        <section5 class="section5" />
        <section6 class="section6" />
    </div>
</template>
<script>
import navbar from "./sections/navbar.vue";
import section1 from "./sections/section1.vue";
import section2 from "./sections/section2.vue";
import section3 from "./sections/section3.vue";
import section4 from "./sections/section4.vue";
import section5 from "./sections/section5.vue";
import section6 from "./sections/section6.vue";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
    components: {
        section1,
        section2,
        section3,
        section4,
        section5,
        section6,
        navbar,
    },
    data() {
        return {
            logoIsShow: true,
            navbarIsShow: false,
        };
    },
    mounted() {
        gsap.registerPlugin(ScrollTrigger);
        ScrollTrigger.create({ trigger: ".logo_div", pin: true, marker: true });
    },
    methods: {
        listClick() {
            console.log("listClick");
            this.logoIsShow = false;
            this.navbarIsShow = true;
        },
    },
};
</script>

<style></style>
