<script>
import isotope from "vueisotope";
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
    components: {
        isotope,
    },
    data() {
        return {
            sceenWidth: screen.width,
            sceenHeight: screen.height,
            ratio: window.innerWidth / window.innerHeight,
            list: [
                { name: "John", id: 25 },
                { name: "Joao", id: 7 },
                { name: "Albert", id: 12 },
                { name: "Jean", id: 100 },
            ],
            selected: null,
            option: {
                getSortData: {
                    id: "id",
                },
                sortBy: "id",
            },
            count: 0,
        };
    },
    mounted() {
        gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);
    },
    methods: {
        btn() {
            gsap.to(".test", { duration: 0.2, ease: "Power4.easeOut", width: "35%" });
            gsap.to(".test1", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
            gsap.to(".test2", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
        },
        btn1() {
            gsap.to(".test1", { duration: 0.2, ease: "Power4.easeOut.", width: "35%" });
            gsap.to(".test", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
            gsap.to(".test2", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
        },
        btn2() {
            gsap.to(".test2", { duration: 0.2, ease: "Power4.easeOut.", width: "35%" });
            gsap.to(".test1", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
            gsap.to(".test", { duration: 0.2, ease: "Power4.easeOut", width: "20%" });
        },
        add: function() {
            this.list.push({ name: "Juan", id: ++this.count });
        },
        replace: function() {
            this.list = [
                { name: "Edgard", id: ++this.count },
                { name: "James", id: ++this.count },
            ];
        },
    },
};
</script>

