                            <div style="overflow:hidden">
                                <p class="new" style="text-shadow: -1px 0 gray, 0 1px white, 1px 0 gray, 0 -1px gray;text-align:center;line-height:9vw; letter-spacing:0.75vw;font-family:cream;font-size:9vw;color:black;margin:0;clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);">
                                    NEW
                                </p>
                            </div>
                            <div style="overflow:hidden;margin-top:5%">
                                <p class="new" style="font-family:cream;font-size:1.5vw;;line-height:1.5vw;color:black;margin-bottom:1vw;clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);margin-top:2%">
                                    時尚在於當下及未來
                                </p>
                            </div>
                            <div style="overflow:hidden">
                                <!-- <p class="new" style="font-family:cream;font-size:1.25vw;color:black;margin:0;clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);">Fashion is about today and tomorrow.</p> -->
                                <button class="btn btn-light new s2" style="font-family:cream;;line-height:1vw;clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);margin-top:5%">SHOP ALL</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>

        <template v-if="ratio >= 0.67 && ratio < 1.2">
            <img class="img-fluid pic1" src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9083-去背-min.png" style="height:50vh;position:absolute;margin-top:42vh " />
            <div class="text-center" style="width:100%;position:absolute;overflow:hidden;left:15%;top:-7%">
                <div class="pic2">
                    <img src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9031-min-1.png" style="height:50vh;position:absolute;position:absolute" />
                    <img src="../../../assets/images/page-home/section-new-arrival/組1/影子-min-3.png" style="height:50vh;" />
                </div>
            </div>

            <img class="img-fluid pic3" src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9083-去背2-min.png" style="height:70vh;position:absolute;right:0%;margin-top:30vh" />
            <div class="col" style="margin-top:10%">
                <div class="row no-gutters justify-content-start" style="padding-left:7%">
                    <div class="" style="position:absolute;margin-top:5vw">
                        <div style="overflow:hidden">
                            <p class="new-mb" style="font-family:cream;font-size:9vw;;line-height:9vw;margin:0; ">NEW</p>
                        </div>
                        <div style="overflow:hidden">
                            <p class="new-mb" style="font-family:cream;font-size:1.5vw;line-height:1.5vw;margin-top:15%">時尚在於當下及未來</p>
                        </div>
                        <div style="overflow:hidden">
                            <button class="new-mb btn btn-light" style="font-size:1vw;line-height:1vw;font-family:cream;margin-top:15%">SHOP ALL</button>
                        </div>
                    </div>
                </div>
            </div>
        </template>

        <template v-if="ratio < 0.67">
            <img class="img-fluid pic1" src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9083-去背-min.png" style="height:40vh;position:absolute;margin-top:40vh " />
            <div class="text-right" style="width:100%;position:absolute;overflow:hidden;left:10%;top:3%">
                <div class="pic2">
                    <img src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9031-min-1.png" style="height:45vh;position:absolute;position:absolute" />
