                </div>
            </div>
            <!-- <img class="img-fluid " src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9031-min.png" style="height:40vh;position:absolute;margin-top:7vh;left:50vw " /> -->
            <img class="img-fluid pic3" src="../../../assets/images/page-home/section-new-arrival/組1/20210115-DSCF9083-去背2-min.png" style="height:50vh;position:absolute;right:0%;margin-top:50vh" />
            <div class="col" style="margin-top:10%">
                <div class="row no-gutters justify-content-start" style="padding-left:7%">
                    <div class="" style="position:absolute;margin-top:15%">
                        <div style="overflow:hidden">
                            <p class="new-mb" style="font-family:cream;font-size:50px;;line-height:45px;margin:0">NEW</p>
                        </div>
                        <div style="overflow:hidden;margin-top:25%">
                            <p class="new-mb" style="font-family:cream;font-size:15px;line-height:15px;margin:0">時尚在於當下及未來</p>
                        </div>
                        <div style="overflow:hidden;margin-top:25%">
                            <button class="new-mb btn btn-light" style="font-size:15px;line-height:15px;font-family:cream;margin:0">SHOP ALL</button>
                        </div>

                        <!-- <p style="font-family:cream;font-size:15vw;margin:0">NEW</p>
                        <p style="font-family:cream;font-size:10px;margin-top:15%">時尚在於當下及未來</p>

                        <button class="btn btn-light" style="font-size:0.75vw;font-family:cream;margin-top:15%">SHOP ALL</button> -->
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
    props: {
        swiperState: {
            type: Object,
        },
    },
    data() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,

            ratio: window.innerWidth / window.innerHeight,
        };
    },

    mounted() {
        gsap.registerPlugin(ScrollTrigger);
        // this.textOut();
        // this.textIn2();
    },
    watch: {
        swiperState(newValue) {
            // console.log(newValue);
            if (newValue.index != 1) {
                if (document.querySelector(".new")) {
                    gsap.to(".new", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 300 });
                }

                if (document.querySelector(".new-mb")) {
                    gsap.to(".new-mb", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 90 });
                }
                if (document.querySelector(".pic1")) {
                    gsap.to(".pic1", { duration: 1.5, ease: "Power4.out", overwrite: true, objectPosition: -800 });
                }
                if (document.querySelector(".pic2")) {
                    gsap.to(".pic2", { duration: 1.5, ease: "Power4.out", overwrite: true, x: 1200 });
                }
                if (document.querySelector(".pic3")) {
                    gsap.to(".pic3", { duration: 1.5, ease: "Power4.out", overwrite: true, objectPosition: 1000 });
                }
            }
            if (newValue.index == 1) {
                this.pic1In();
                this.pic2In();
                this.pic3In();
                if (document.querySelector(".new")) {
                    gsap.to(".new", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 300 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".new-mb")) {
                    gsap.to(".new-mb", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 90 }, rotation: 0, y: 0 });
                }
                // if (oldvalue.index == 0) {
                //     // 從上至下
                //     console.log("從上至下");
                // }
                // if (oldvalue.index == 2) {
                //     // 從上至下
                //     console.log("Changed");
                // }
            }
        },
    },

    methods: {
        pic1In() {
            gsap.to(".pic1", {
                ease: "power4.out",
                startAt: { objectPosition: -800 },

                duration: 1.5,
                objectPosition: "0px",
                overwrite: true,
            });
        },
        pic2In() {
            gsap.to(".pic2", {
                ease: "power4.out",
                duration: 1.5,
                x: 0,
                startAt: { x: 1200 },
                objectPosition: "0px",
                overwrite: true,
            });
        },
        pic3In() {
            gsap.to(".pic3", {
                ease: "power4.out",
                duration: 1.5,
                startAt: { objectPosition: 1000 },
                objectPosition: "0px",
                overwrite: true,
            });
        },
    },
};
</script>

<style scoped>
.new {
    transform: translate(0px, 300px);
}
.new-mb {
    transform: translate(0px, 90px);
}
.pic1 {
    object-position: -800px;
}
.pic2 {
    transform: translate(1200px, 0px);
}
.pic3 {
    object-position: 1000px;
}
.section {
    background: #efeff4;
    height: 100vh;
}
</style>
