<script>
import { gsap } from "gsap";

export default {
    props: {
        swiperState: {
            type: Object,
        },
    },
    data() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,

            ratio: window.innerWidth / window.innerHeight,
            border: "font-family:cream;border-radius:0;border:1px black solid;",
        };
    },
    watch: {
        swiperState(newValue) {
            if (newValue.index != 5) {
                if (document.querySelector(".about-us")) {
                    gsap.to(".about-us", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 350 });
                }

                if (document.querySelector(".about-us-mb")) {
                    gsap.to(".about-us-mb", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 100 });
                }
            }
            if (newValue.index == 5) {
                if (document.querySelector(".about-us")) {
                    gsap.to(".about-us", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 350 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".about-us-mb")) {
                    gsap.to(".about-us-mb", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 100 }, rotation: 0, y: 0 });
                }
                // if (oldvalue.index == 0) {
                //     // 從上至下
                //     console.log("從上至下");
                // }

                // if (oldvalue.index == 2) {
                //     // 從上至下
                //     console.log("Changed");
                // }
            }
        },
    },
    mounted() {},
    methods: {},
};
</script>

<style scoped>
.about-us-mb {
    transform: translate(0px, 100px);
}
.about-us {
    transform: translate(0px, 350px);
}
