<script>
import { gsap } from "gsap";

export default {
    props: {
        swiperState: {
            type: Object,
        },
    },
    data() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,
            // ratio: screen.width / screen.height,
            ratio: window.innerWidth / window.innerHeight,
        };
    },
    mounted() {},
    watch: {
        swiperState(newValue) {
            if (newValue.index != 3) {
                if (document.querySelector(".combination")) {
                    gsap.to(".combination", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 350 });
                }
                if (document.querySelector(".combination-pad")) {
                    gsap.to(".combination-pad", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 225 });
                }
                if (document.querySelector(".combination-mb")) {
                    gsap.to(".combination-mb", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: -25, y: 150 });
                }
            }
            if (newValue.index == 3) {
                if (document.querySelector(".combination")) {
                    gsap.to(".combination", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 350 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".combination-pad")) {
                    gsap.to(".combination-pad", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: 25, y: 225 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".combination-mb")) {
                    gsap.to(".combination-mb", { ease: "Power4.out", duration: 0.75, overwrite: true, startAt: { rotation: -25, y: 150 }, rotation: 0, y: 0 });
                }
                // if (oldvalue.index == 0) {
                //     // 從上至下
                //     console.log("從上至下");
                // }

                // if (oldvalue.index == 2) {
                //     // 從上至下
                //     console.log("Changed");
                // }
            }
        },
    },
};
</script>

