<script>
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
    props: {
        swiperState: {
            type: Object,
        },
    },
    data() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,
            ratio: window.innerWidth / window.innerHeight,
        };
    },
    mounted() {
        gsap.registerPlugin(ScrollTrigger);
    },
    watch: {
        swiperState(newValue) {
            // console.log(newValue.index);
            if (newValue.index != 2) {
                if (document.querySelector(".new2")) {
                    gsap.to(".new2", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 20, y: 300 });
                }
                if (document.querySelector(".new2-pad")) {
                    gsap.to(".new2-pad", { duration: 1.5, ease: "Power4.out", overwrite: true, rotation: 25, y: 100 });
                }
                if (document.querySelector(".pic4")) {
                    gsap.to(".pic4", { duration: 1.5, ease: "Power4.out", overwrite: true, objectPosition: "0px 1000px" });
                }
                if (document.querySelector(".pic5")) {
                    gsap.to(".pic5", { duration: 1.5, ease: "Power4.out", overwrite: true, objectPosition: "0px -1000px" });
                }
                if (document.querySelector(".pic6")) {
                    gsap.to(".pic6", { duration: 1.5, ease: "Power4.out", overwrite: true, objectPosition: "0px 1000px" });
                }
            }
            if (newValue.index == 2) {
                this.pic4In();
                this.pic5In();
                this.pic6In();
                if (document.querySelector(".new2")) {
                    gsap.to(".new2", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 20, y: 300 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".new2-pad")) {
                    gsap.to(".new2-pad", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 25, y: 100 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".new2-mb")) {
                    gsap.to(".new2-mb", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 25, y: 75 }, rotation: 0, y: 0 });
                }
                // if (oldvalue.index == 0) {
                //     // 從上至下
                //     console.log("從上至下");
                // }

                // if (oldvalue.index == 2) {
                //     // 從上至下
                //     console.log("Changed");
                // }
            }
        },
    },

    methods: {
        pic4In() {
            gsap.to(".pic4", {
                ease: "power4.out",
                startAt: { objectPosition: "0px 1000px" },

                duration: 1.5,
                objectPosition: "0px 0px",

                overwrite: true,
            });
        },
        pic5In() {
            gsap.to(".pic5", {
                ease: "power4.out",
                // delay: 0.25,
                duration: 1.5,
                x: 0,
                startAt: { objectPosition: "0px -1000px" },

                objectPosition: "0px 0px",
                overwrite: true,
            });
        },
        pic6In() {
            gsap.to(".pic6", {
                ease: "power4.out",
                duration: 1.5,
                startAt: { objectPosition: "0px 1000px" },
                objectPosition: "0px 0px",

                overwrite: true,
            });
        },
    },
};
</script>

<style scoped>
.new2 {
    transform: translate(0px, 300px);
}
.new2-pad {
    transform: translate(0px, 100px);
}
.new2-mb {
    transform: translate(0px, 75px);
}
.pic4 {
    object-position: "0px 1000px";
}
.pic5 {
    object-position: "0px -1000px";
}
.pic6 {
    object-position: "0px 1000px";
}
.section {
    background: efeff4;
    height: 100vh;
}
</style>
